.content{background-color:#fff6f6}.content h1{color:#333;text-align:center;padding-top:50px}.content hr{width:40px;height:2px;background:#6a00a1;border:none;border-radius:12px;margin:4px auto}.content .graduateContent{display:flex;align-items:center;justify-content:space-between;padding:50px 0}.content .graduateContent .title{max-width:50%;font-family:"Roboto",sans-serif;color:#333}.content .graduateContent .title p{font-size:1.5rem;line-height:1.5;font-weight:normal}.content .graduateContent .title span{font-style:italic;display:block;margin-top:20px;font-size:.9rem;font-weight:300;color:#6a6a6a}.content .graduateContent .wrapper{display:flex;align-items:center;justify-content:space-around;width:50%;gap:80px;padding-top:100px}.content .graduateContent .wrapper svg{cursor:pointer;font-size:1.5rem;color:#555}.content .graduateContent .wrapper svg:hover{color:#000}.content .graduateContent .image{max-width:30%}.content .graduateContent .image img{width:100%;box-shadow:0px 4px 15px rgba(0,0,0,.1);object-fit:cover}@media(max-width: 430px){.content .graduateContent{flex-direction:column-reverse;align-items:center;justify-content:center}.content .graduateContent .title{padding-top:10px;max-width:80%}.content .graduateContent .title p{font-size:16px}.content .graduateContent .wrapper{width:100%;padding-top:20px;gap:20px}.content .image{display:flex;align-items:center;justify-content:center}.content .image img{height:400 !important;width:300 !important}}@media(max-width: 884px){.content .graduateContent{padding:60px}.content .graduateContent .title{max-width:80%}.content .graduateContent .title p{font-size:16px}.content .wrapper{width:100%;padding-top:300px !important;gap:20px}.content .image{display:flex;align-items:center;justify-content:center;margin-right:20px}.content .image img{height:340 !important;width:340 !important}}